@import '~terra-mixins/lib/Mixins';

:global {
  --terra-clinical-data-grid-background-color: #2d3539;
  --terra-clinical-data-grid-border: 1px solid #181b1d;

  --terra-clinical-data-grid-cell-border-right: 1px solid #181b1d;
  --terra-clinical-data-grid-cell-hover-background-color: rgba(0, 0, 0, 0.36);
  --terra-clinical-data-grid-cell-selected-background-color: #1e3a49;
  --terra-clinical-data-grid-cell-selected-hover-background-color: #1e3a49;

  --terra-clinical-data-grid-column-header-background-color: #141719;
  --terra-clinical-data-grid-column-header-border-bottom: 2px solid #181b1d;
  --terra-clinical-data-grid-column-header-text-color: #b2b5b6;

  --terra-clinical-data-grid-header-cell-border-right: 1px solid #181b1d;

  --terra-clinical-data-grid-pinned-column-box-shadow: 1px 0 5px #181b1d;

  --terra-clinical-data-grid-resize-handle-active-background-color: rgba(34, 42, 46, 1);
  --terra-clinical-data-grid-resize-handle-hover-background-color: rgba(34, 42, 46, 0.6);

  --terra-clinical-data-grid-row-background-color: #383f42;
  --terra-clinical-data-grid-row-hover-background-color: #232a2d;
  --terra-clinical-data-grid-row-selected-background-color: #1e3a49;
  --terra-clinical-data-grid-row-selected-box-shadow: inset 0 1px 0 0 #004c76, inset 0 -1px 0 0 #004c76;
  --terra-clinical-data-grid-row-selected-hover-background-color: #1e3a49;
  --terra-clinical-data-grid-row-striped-background-color: #2d3539;

  --terra-clinical-data-grid-scrollbar-background-color: #2d3539;
  --terra-clinical-data-grid-scrollbar-pinned-column-box-shadow: 1px 0 5px #181b1d, inset -1px 0 0 0 #181b1d;

  --terra-clinical-data-grid-section-header-background-color: #222a2e;
  --terra-clinical-data-grid-section-header-border-bottom: 1px solid #181b1d;
  --terra-clinical-data-grid-section-header-hover-background-color: none;

  --terra-clinical-data-grid-cell-active-box-shadow: none;

  --terra-clinical-data-grid-cell-focus-background-color: #1e3a49;
  --terra-clinical-data-grid-cell-focus-box-shadow: inset 0 0 0 1px #004c76, inset 0 0 0 4px #004c76;

  --terra-clinical-data-grid-cell-selected-box-shadow: inset 0 0 0 1px #004c76, inset 0 0 0 4px #004c76;
  --terra-clinical-data-grid-cell-selected-focus-background-color: #1e3a49;
  --terra-clinical-data-grid-cell-selected-focus-box-shadow: inset 0 0 0 1px #004c76, inset 0 0 0 4px #004c76;

  --terra-clinical-data-grid-column-header-sort-indicator-color: #b2b5b6;

  --terra-clinical-data-grid-scrollbar-handle-active-background-color: #000;
  --terra-clinical-data-grid-scrollbar-handle-background-color: #181b1d;

  --terra-clinical-data-grid-section-header-border-top: 1px solid #181b1d;
  --terra-clinical-data-grid-section-header-collapsible-icon-color: #b2b5b6;
  --terra-clinical-data-grid-section-header-color: #b2b5b6;
  --terra-clinical-data-grid-section-header-focus-box-shadow: inset 0 0 0 3px #004c76;

  @include terra-inline-svg-var('--terra-clinical-data-grid-row-selection-icon-background', '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48"><path fill="#000" d="M42,4H6C4.9,4,4,4.9,4,6v36c0,1.1,0.9,2,2,2h36c1.1,0,2-0.9,2-2V6C44,4.9,43.1,4,42,4z"/><path fill="#b2b5b6" d="M42,4c1.1,0,2,0.9,2,2v36c0,1.1-0.9,2-2,2H6c-1.1,0-2-0.9-2-2V6c0-1.1,0.9-2,2-2H42 M42,0H6C2.7,0,0,2.7,0,6v36 c0,3.3,2.7,6,6,6h36c3.3,0,6-2.7,6-6V6C48,2.7,45.3,0,42,0L42,0z"/></svg>');
  @include terra-inline-svg-var('--terra-clinical-data-grid-selected-row-selection-icon-background', '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48"><path fill="#003f61" d="M42,4H6C4.9,4,4,4.9,4,6v36c0,1.1,0.9,2,2,2h36c1.1,0,2-0.9,2-2V6C44,4.9,43.1,4,42,4z"/><path fill="#004c76" d="M42,4c1.1,0,2,0.9,2,2v36c0,1.1-0.9,2-2,2H6c-1.1,0-2-0.9-2-2V6c0-1.1,0.9-2,2-2H42 M42,0H6C2.7,0,0,2.7,0,6v36   c0,3.3,2.7,6,6,6h36c3.3,0,6-2.7,6-6V6C48,2.7,45.3,0,42,0L42,0z"/><path fill="#b2b5b6" d="M20,36.4L6.7,23.1l3.6-3.6l9.7,9.9l17.7-17.9l3.6,3.6L20,36.4z"/></svg>');
}
